import { Link } from "react-router-dom"

export const Nav = () => {

    const handleMobileMenu = () => {
        document.querySelector('.nav-links').classList.toggle('nav-links-visible')
    }

    const CloseMenuOnLinksClick = () => {
        document.querySelector('.nav-links').classList.toggle('nav-links-visible')
    }

    return <nav>
        <div className="nav-logo">
            <img src={require('../Assets/images/logo.png').default} />
        </div>

        <div className="nav-mobileBtn">
            <i className="mdi mdi-menu" onClick={() => handleMobileMenu()}></i>
        </div>

        <div className="nav-links">
            <Link to="/"
                onClick={() => CloseMenuOnLinksClick()}>Accueil</Link>
            <Link to="/"
                onClick={() => CloseMenuOnLinksClick()}>A propos</Link>
            <Link to="/Publications"
                onClick={() => CloseMenuOnLinksClick()}>Offres d'emplois</Link>
            <Link to="/Contact"
                onClick={() => CloseMenuOnLinksClick()}>Contact</Link>
            <div className="nav-authLinks">
                <Link className="semiRounded" to="/Authentification/Connexion"
                    onClick={() => CloseMenuOnLinksClick()}>Connexion</Link>
                <Link className="semiRounded" to="/Authentification/Inscription"
                    onClick={() => CloseMenuOnLinksClick()}>Inscription</Link>
            </div>
        </div>

    </nav>
}